import { html } from 'lit';

export default {
  title: 'Mega Puzzle/Button',
  component: 'Button',
  id: 'btn',
  args: { label: "I'm an anchor tag", href: "https://reeceneedham.com", primary: false, openInNewTab: false}
};

export const anchor = {
  args: { label: "I'm an anchor tag", href: "https://reeceneedham.com", openInNewTab: false, primary: false},
  render: ({label, href, openInNewTab, primary}) => {
    const isPrimary = primary ? `class="primary"` : ``
    return openInNewTab ? html`<a${isPrimary} href="${href}" target="_blank" rel="noopener">${label}</a>`: html`<a href="${href}">${label}</a>`;
  }
};

export const button = {
  args: { label: "I'm a button!", primary: true},
  render: ({label, primary}) => {
    const isPrimary = primary ? ` class="primary"` : ``
    return html`<button ${isPrimary}>${label}</button>`
  }
};
